*{
    text-align: center;
}

.jumbotron { 
    background-image: url(../img/back.jpg);
    background-size: cover;
    height: 300px;
    display: table-cell;
    vertical-align: middle;
    h2, p {
        text-align: center;
        color: white;
    }
}

.alert {
    max-width: 400px;
    width: 100%;
    background-color: lightgoldenrodyellow;
    padding: 10px;
    border-radius: 5px;
    margin: 20px auto;
    text-align: center;
    font-size: smaller;
    p{
        margin-bottom: 0;
    }
}

.alertBye{
    background-color: white;
    color: white;
}

