.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.black-nav{
  background-color: black;
  color: white;
  text-align: left;
  padding: 20px;
  display: flex;
  font-weight: bold;
}

.list{
  text-align: left;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 30px;
  font-size: 0.8rem;
}

.modal{
  background-color: rgb(236, 236, 236);
  padding: 20px;
  margin-top: 20px;
}

.input{
  margin: 20px;
}

.input input{
  font-size: 20px;
  padding: 5px;
  margin-right: 10px;
}

.input button{

}